* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Inter", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
}

body {
    min-height: 100vh;
    background: radial-gradient(circle at 20% 20%, #f4f7ff, #e9eefb 40%, #dce3f5 70%, #d4ddf2);
    color: #0f172a;
}

.page {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 32px 16px;
}

.card {
    width: min(480px, 100%);
    background: #ffffff;
    box-shadow: 0 24px 70px rgba(15, 23, 42, 0.08);
    border: 1px solid #e2e8f0;
    border-radius: 20px;
    padding: 32px;
}

.card header {
    margin-bottom: 20px;
}

.card h1 {
    font-size: 28px;
    font-weight: 700;
    letter-spacing: -0.02em;
    margin-bottom: 6px;
}

.card p {
    color: #475569;
    line-height: 1.6;
}

.login-form {
    display: grid;
    gap: 12px;
    margin-top: 16px;
}

.label {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    color: #475569;
}

.input {
    width: 100%;
    padding: 12px 14px;
    border-radius: 12px;
    border: 1px solid #cbd5e1;
    background: #f8fafc;
    transition: border-color 0.2s, box-shadow 0.2s, background 0.2s;
}

.input:focus {
    outline: none;
    border-color: #4f46e5;
    background: #fff;
    box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.15);
}

.hint {
    font-size: 13px;
    color: #64748b;
}

.submit-btn {
    margin-top: 8px;
    padding: 12px 14px;
    border: none;
    border-radius: 12px;
    background: linear-gradient(135deg, #4f46e5, #7c3aed);
    color: #fff;
    font-weight: 600;
    cursor: pointer;
    transition: transform 0.1s ease, box-shadow 0.2s ease, opacity 0.2s ease;
}

.submit-btn:hover {
    transform: translateY(-1px);
    box-shadow: 0 14px 30px rgba(79, 70, 229, 0.35);
}

.submit-btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
}

.status {
    margin-top: 10px;
    padding: 12px 14px;
    border-radius: 12px;
    font-size: 14px;
    line-height: 1.5;
}

.status.success {
    background: #f0fdf4;
    color: #15803d;
    border: 1px solid #bbf7d0;
}

.status.error {
    background: #fef2f2;
    color: #b91c1c;
    border: 1px solid #fecdd3;
}

.info {
    margin-top: 18px;
    padding: 12px 14px;
    border-radius: 12px;
    background: #f8fafc;
    border: 1px solid #e2e8f0;
    color: #475569;
    font-size: 14px;
    line-height: 1.6;
}
